@import './../../scss/variables.scss';
@import './../../scss/mixins.scss';

.contact {
  @include bg-color-gradient($grad-colors-6);
  
  .fp-tableCell {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}

.contact__container {
  width:100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-grow: 1;

  @media all and (min-width: 720px) {
    margin: 0 auto;
    max-width: 900px;
  } 
}
.contact__title {
  margin-bottom: 50px;

  @media all and (min-width: 720px) {
    text-align: left;
    align-self: flex-start;
    padding: 0 30px;
  }
  @media all and (min-width: 1024px) {
    padding: 0;
  }
}
.contact__text {
  margin: 0;

  & + .contact__text {
    margin-bottom: 50px;
  }
}

.contact__list {
  list-style: none;
  width: 100%;
  max-height: 300px;
  flex-grow: 1;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-column-gap: 6px;
  grid-row-gap:20px;
}
.contact__link, .footer__link {
  text-decoration: none;
  color: $white;
  
  .link__text {
    margin: 10px;
    
    @media all and (min-width: 720px) {
      display: block;
    }
  }
  
  &:hover {
    color: $grad-light-blue;
    .link__text {
      font-weight: bold;
    }  
  }
}
.contact__icon {
  @media all and (min-width: 720px) {
    font-size: 50px;
  }
}
.page__footer {
  margin-top: 70px;
  padding: 30px 0;
  width:100%;
  border-top: 1px solid $grad-light-blue;
  
  .footer__text {
    @media all and (max-width: 720px) {
      display: block;
    }    
    line-height: 30px;
  }
  
  .footer__link {
    font-weight: bold;
    color: $grad-light-blue;
    
    &:hover {
      color: $grad-light-orange;
    }
  }
}